import React,{Component} from "react";
import Todolist from "./components/todolist/todolist";
import Toaddlist from "./components/toaddlist/toaddlist";

class App extends Component{
  state = {
    items:[
      {id:1,name:'ahmed',age:20,job:'teacher'},
      {id:2,name:'eyad',age:25,job:'programmer'},
      {id:3,name:'mohamed',age:30,job:'engineer'}

    ]
  }
  deleteItems=(id)=>{
let items=this.state.items.filter(item=>{
  return item.id !==id
})

this.setState({items})

}
addItems=(item)=>{
  item.id=Math.random()
let items=this.state.items
items.push(item)
this.setState({items})
}
  render(){
  return (
    <div className="App container">
      <h1 className="text-center">This is Todo List APP</h1>
      <Todolist items={this.state.items} deleteItems={this.deleteItems}/>
      <Toaddlist addItems={this.addItems}/>
    </div>
  );
}
}

export default App;
