import React, { Component } from  "react";
import './toaddlist.css'
class Toaddlist extends Component{
    state={
        name:"",
        age:"",
        job:""
    }
    handlechange=(e)=>{
         this.setState({
        [e.target.id] : e.target.value})
    }
    handlesubmit=(e)=>{
        e.preventDefault()
      if(e.target.name.value===''){
        return false
     }else{
        this.props.addItems(this.state)
        this.setState({
            name:'',
            age:"",
            job:""
        })
     }
     }
      
    render(){
        return(
            <div className='listitems'>
                <form onSubmit={this.handlesubmit}>
                    <input type="text" placeholder="Enter Your Name" id="name"onChange={this.handlechange} value={this.state.name}/>
                    <input type="text" placeholder="Enter Your Job" id="job" onChange={this.handlechange} value={this.state.job}/>
                    <input type="number" placeholder=" Your Age"id = "age" onChange={this.handlechange} value={this.state.age}/>
                    <input type="submit" value ="Add"/>

                </form>
            </div>
        )
    }
}
export default Toaddlist