import React from "react";
import "./todolist.css"
const Todolist =(props)=>{
    const {items,deleteItems}=props
    let length=items.length

    const Listitems=length?(
        items.map(item=>{
            return(
                <div key={item.id}>
                    <span className="name">{item.name}</span>
                    <span className="job">{item.job}</span>
                    <span className="age">{item.age}</span>
                    <span className='action icon' onClick={()=>deleteItems(item.id)}>&times;</span>
    
                </div>
            )
        })
    ):(<p>There isn't Any Items To Show</p>)
    return(
        <div className="listitems">
            <div>
            <span className="title name">Name </span>
            <span className="title job">Job </span>
            <span className="title age">Age </span>
            <span className="title action">Action </span>
            </div>
            {Listitems}
        </div>
    )
}
export default Todolist